import './auth.scss'
import GoogleIcon from '@mui/icons-material/Google';
import { useNavigate, Link } from 'react-router-dom';
import { Backbutton } from '../BackButton/BackButton';
import { useEffect, useState } from 'react';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { useForm, } from "react-hook-form";
import { Spinner } from '../Spinner/Spinner';


export const Register = ({currentUser, langEn, setCurrentUser, signInWithGoogle}) => {
  const navigate = useNavigate()
//     
  const [emailExists, setEmailExists] = useState('')

    //   
    const [showSpinner, setShowSpinner] = useState(false);

  //     
  useEffect(()=>{
    setTimeout(()=>{
      if (emailExists !=='')
      setEmailExists('')
    }, 5000)
  }, [emailExists])

  //     Google
  const onSignInWithGoogle = () => {
    signInWithGoogle();
    navigate('/')
  }


  //    Firebase
  const auth = getAuth();
  const user = auth.currentUser


//    
  const {register, handleSubmit, getValues, formState: { errors }} = useForm({ mode: "onSubmit" });

  const emailRegisterEn = register("email", {
    required: "Email required",
    pattern: {
      message: "Incrorrect email!",
      value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g
    }
  });

  const emailRegisterRu = register("email", {
    required: "Email ",
    pattern: {
      message: " email!",
      value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g
    }
  });




useEffect(()=>{
  if (emailExists)
  setShowSpinner(false)
},
[emailExists])




// Register  
  const passwordRegisterEn = register("password", {
    required: "Password required",
    pattern: {
      message:
      "Your password must be not shorter than 6 characters and have at least one upper case English letter.",
      value: /(?=.*?[A-Z])/g
    },
    minLength: {
      value:6,
      message:
      "Your password must be not shorter than 6 characters",
      }
  });

  const passwordRegisterRu = register("password", {
    required: " ",
    pattern: {
      message:
      "      6          ",
      value: /(?=.*?[A-Z])/g
    },
    minLength: {
      value:6,
      message:
      "      6 ",
      }
  });
//  
  const passwordConfirmEn = register('passwordConfirm', {
    required: true,
      validate: (value) => {
        const {password} = getValues();
        return password === value || 'Passwords do not match'
        },
      }
  )
  const passwordConfirmRu = register('passwordConfirm', {
    required: true,
      validate: (value) => {
        const {password} = getValues();
        return password === value || '  '
        },
      }
  )  

//    
async function RegisterWithEmailPassword(email, password) {
  createUserWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      const user = userCredential.user;
      setCurrentUser(user);
      navigate('/user-settings')
    })
    .catch((error) => {
      const errorCode = error.code;
      if (errorCode === 'auth/email-already-in-use' && langEn) 
      setEmailExists('This email already exists');
      if (errorCode === 'auth/email-already-in-use' && !langEn) 
      setEmailExists(' email  ');
      
    });
  
}


//   
const sendSignUpData = async (data) => {
  setShowSpinner(true)
  await RegisterWithEmailPassword(data.email, data.password)   
}


  return (
    <div>
      <div className='auth_main'>
        {/*  */}
        <div className="auth_container">
          <div onClick={()=>navigate(-1)} className="auth_backbtn"><Backbutton/></div>
          <div className='auth_top'>
          <h1>{langEn ? 'SIGN UP' : ''}</h1>
          <div className='sign-in-label'>
            <span >{langEn ? 'Existing user?' : ' ?'}</span>
            <Link style={{color: 'violet'}} to='/sign-in'>{langEn ? 'SIGN IN' : '  '}</Link>
          </div>
          </div>
          {/*  */}
          <form onSubmit={handleSubmit(sendSignUpData)}>
            <div className='auth_form'>
              {/*   email */}
              <div className='inputs__container'>
                        {langEn ?
                        <div className='single__input__wrapper'>
                          <label >Email</label>
                            <input
                              className='input'
                              type='text'
                              {...emailRegisterEn}
                            >
                            </input>
                        </div>
                        :
                        <div className='single__input__wrapper'>
                        <label >Email</label>
                          <input
                            className='input'
                            type='text'
                            {...emailRegisterRu}
                          >
                          </input>
                      </div>
                      }
                        {langEn ?
                        <div className='single__input__wrapper'>
                          <label>{langEn ? 'Password' : ''}</label>
                            <input
                              className='input'
                              type='password'
                              {...passwordRegisterEn}
                              minLength={6}
                            >
                            </input>
                        </div>
                        :
                        <div className='single__input__wrapper'>
                        <label>{langEn ? 'Password' : ''}</label>
                          <input
                            className='input'
                            type='password'
                            {...passwordRegisterRu}
                            minLength={6}
                          >
                          </input>
                      </div>
                      }
                      {langEn ?
                        <div className='single__input__wrapper'>
                          <label>{langEn ? 'Confirm Password' : ' '}</label>
                            <input
                              className='input'
                              type='password'
                              {...passwordConfirmEn}
                            >
                            </input>
                        </div>
                        :
                        <div className='single__input__wrapper'>
                        <label>{langEn ? 'Confirm Password' : ' '}</label>
                          <input
                            className='input'
                            type='password'
                            {...passwordConfirmRu}
                          >
                          </input>
                      </div>
                      }
                    </div>
              <div className='errors__container'>
                { errors?.email  &&
                  <small className='auth_small'>{errors.email?.message}</small>}
                { emailExists !== '' && <small className='auth_small'>{emailExists}</small>}
                  {errors?.password && (
                    <small className='auth_small'>{errors.password?.message}</small>)}
                  {errors?.passwordConfirm && (
                    <small className='auth_small'>{errors.passwordConfirm?.message}</small>)}
              </div>
                  <a className='policy' target='blank' onClick={()=>{navigate('/privacy-policy')}}>{langEn ? 'Privacy Policy and Rules' : '   '}</a>
              </div>
              
              <div className='auth_sign_btn_wrapper'>
                {/*     */}
                  <button type="submit" className='auth_sign_btn'>{langEn ? 'Create Account' : ' '}</button>
              </div>
          </form> 
        
          {/*       */}
          <button className='auth_sign_btn' onClick={()=>{onSignInWithGoogle()}}>{langEn ? 'Sign in with Google' : '  Google'}<GoogleIcon fontSize='medium'/></button>
          
        <div className='spinner_container'>
          {showSpinner &&
                      <span><Spinner/></span>
                    }
        </div>
        </div>
      </div>
    </div>
  )
}